<template>
    <div style="padding-top:46px">
        <van-nav-bar @click-left="$router.back()" left-arrow :title="'所有房间（ ' +baseDatas.length + ' ）'" right-text="换房" @click-right="clickChange"></van-nav-bar>
        
       <div id="scrollBox" style="overflow-y:auto;height:calc(100% - 46px)">
            <van-tabs @change="changeTab" color="#02A7F0" class="tabs" v-model="active" type="card">
                <van-tab name="0" title="全部"></van-tab>
                <van-tab name="1" title="已入住"></van-tab>
                <van-tab name="2" title="闲置"></van-tab>
            </van-tabs>

            <p class="zhushi">
                <span style="color:#F56C6C"><van-icon name="wap-home" /> 已入住</span>
                <span style="color:#67C23A"><van-icon name="wap-home" /> 闲置</span>
            </p>

            <div class="content">
                 <van-list
                          v-model="loading"
                          :finished="finished"
                          finished-text="没有更多了"
                          @load="onLoad"
                 >
                    <div class="contentBox">
                    <div class="item" @click="clickItem(item,index)" :style="{'backgroundColor':item.F_CheckInPersonalNum > 0 ? '#F56C6C' : '#67C23A'}" 
                             v-for="(item,index) in baseDatas" :key="index">

                        <van-radio icon-size="18px" checked-color="#02A7F0" :name="item.F_RoomNumber" v-model="activeRoom" shape="square"></van-radio>
                        <!-- <van-checkbox v-if="showCheckbox" icon-size="18px" checked-color="#02A7F0" v-model="item.active" shape="square"></van-checkbox> -->
                        
                        <p>{{item.F_RoomNumber}}</p>
                        <!-- v-if="item.userNum > 0" -->
                        <p >({{item.F_CheckInPersonalNum}}人)</p>
                    </div>
                   </div>

                 </van-list>
            </div>


            <div style="width:calc(100% - 1.6rem);margin:1rem auto;text-align:center">
                <!-- <van-button style="width:8rem;margin-right:0.5rem" color="#02A7F0" @click="clickChange">换房</van-button> -->
                <!-- <van-button style="width:8rem;" color="#02A7F0" @click="$router.back()">取消</van-button> -->
            </div>
       </div>
    </div>
</template>

<script>
import { Toast,Dialog } from 'vant';
import {hotellist,checkOut,recomposeroom,allrecomposeroom} from "@/api/house";

    export default {
        data() {
            return {
                active:0,
                baseDatas:[],
                activeRoom:"",


                showCheckbox:false,

                loading:false, 
                finished:false,   
                page:0,

                F_HotelRoomID:"",//房屋id
            }
        },
        methods: {

             //换房
            recomposeroom(){
               
            

            },
            clickChange(){
                if(this.activeRoom == ""){
                    Toast('请选择房间换房');
                }else{
                    Dialog.confirm({
                        title: '换房',
                        message: '确定和' + this.activeRoom + '房间换房吗？',
                    })
                    .then(() => {


                        if(this.$route.query.all==0){
                         let data = {
                             check_in_record_id:this.$route.query.F_CheckInRecordID,
                             hotel_room_id:this.F_HotelRoomID
                            }
                          recomposeroom(data).then((res)=>{
                               if(res.code==0){

                                   // on confirm
                                     Toast.loading({
                                       message: '加载中...',
                                       forbidClick: true,
                                       duration:1000,
                                       onClose:() =>{
                                           Toast.success('换房成功');
                                           this.$router.back()
                                       }
                                   });
                                        
                               }else{
                                     this.$api.msg(res.msg)
                               }
                           })
                        }else{

                             let data = {
                             after_hotel_room_id:this.F_HotelRoomID ,
                             hotel_room_id:this.$route.query.F_CheckInRecordID
                            }
                          allrecomposeroom(data).then((res)=>{
                               if(res.code==0){

                                   // on confirm
                                     Toast.loading({
                                       message: '加载中...',
                                       forbidClick: true,
                                       duration:1000,
                                       onClose:() =>{
                                           Toast.success('换房成功');
                                           this.$router.back()
                                       }
                                   });
                                        
                               }else{
                                    this.$api.msg(res.msg)

                               }
                           })
                           
                        }
                    })
                    .catch(() => {
                        // on cancel
                    });
                }
            },

        onLoad(){
              this.page++;
                let parans = {
                  page: this.page,
                  check_in_personal_num:this.active==0?"":this.active==1?1:0
               };
               hotellist(parans).then((res) => {
            
                   if(res.code==0){
                      console.log(res.list);
                      this.baseDatas = [...this.baseDatas,...res.list]; //列表
                      this.loading = false;

               // 数据全部加载完成
              if (res.list.length < 10) {
                this.finished = true;
              }

            }else{

              this.$api.msg(res.msg)
            }
          
        })
    },
            clickItem(item,index){
                
                this.activeRoom = item.F_RoomNumber,
                this.F_HotelRoomID = item.F_HotelRoomID
            },
            changeTab(name){
                this.finished = false;
                this.page = 0;
                this.baseDatas = [];


            },
            getRooms() {
                // let arr = Array.from({length:49}).map((item,index) =>{
                //     return {
                //         roomKey: (parseInt(index / 10) + 1) * 1000 + (index % 10 > 0 ? index % 10 : 0) + 1,
                //         userNum: Math.ceil(Math.random() * 4) - 1
                //     }
                // })
                // return arr
            }
        },
        mounted () {
            this.baseData = this.getRooms();
        },
    }
</script>

<style lang="scss" scoped>
.van-nav-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
.content{
    margin-top: 1rem;
    padding: 0 1rem;
    .contentBox{
        width: 100%;
        display: flex;
        justify-content:center;
        flex-wrap:wrap;
        .item{
               float: left;
            width: 4rem;
            padding: 0.3rem 0.3rem 0.3rem 0.3rem;
            margin: 0.28rem;
            p{
                margin: 0;
                color: #fff;
                text-align: center;
                font-size: 0.8rem;
                line-height: 1.4rem;
            }
        }
    }
}

.tabs{
    margin: 1rem 0;
}

.zhushi{
    margin: 0;
    text-align: center;
    span{
        &:nth-child(1){
            margin-right: 4rem;
        }
    }
    .van-icon{
        position: relative;
        top: 0.15rem;
    }
}
</style>